<template>
  <el-container class="setting-wrap">
    <el-header class="head">
      <h2 class="pgtit">组织设置</h2>
    </el-header>
    <el-container>
      <el-main class="main-inner main-set">
        <h3>客服</h3>
        <el-form :model="form" ref="form">
          <el-form-item class="set-item" v-for="(service, i) in form.services" :key="i">
            <el-input v-model="service.name" :placeholder="'请输入客服名字' + i"></el-input>
            <el-input v-model="service.url" :placeholder="'请输入链接' + i"></el-input>
            <el-button type="primary" @click.prevent="saveItem()">保存</el-button>
            <el-button type="danger" @click.prevent="removeItem(service)">删除</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addItem">新增客服</el-button>
          </el-form-item>
        </el-form>
        <el-divider />
        <h3>游戏</h3>
        <el-form :model="form" ref="form">
          <el-form-item class="set-item" v-for="(game, i) in form.games" :key="i">
            <el-input v-model="game.name" :placeholder="'请输入游戏名字' + i"></el-input>
            <el-input v-model="game.url" :placeholder="'请输入链接' + i"></el-input>
            <el-button type="primary" @click.prevent="saveItem()">保存</el-button>
            <el-button type="danger" @click.prevent="removeItem(game, 'game')">删除</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addItem('game')">新增游戏</el-button>
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
    <el-footer class="foot" />
  </el-container>
</template>

<script>
export default {
  name: "Create",
  data() {
    return {
      form: {
        services: [
          {
            name: "名称1",
            url: "https://www.hehe1.com",
            id: 1
          },
          {
            name: "名称2",
            url: "https://www.hehe2.com",
            id: 2
          },
          {
            name: "名称3",
            url: "https://www.hehe3.com",
            id: 3
          }
        ],
        games: [
          {
            name: "游戏名称1",
            url: "https://www.hehe1.com",
            id: 1
          },
          {
            name: "游戏名称2",
            url: "https://www.hehe2.com",
            id: 2
          }
        ]
      }
    };
  },
  mounted() {},
  components: {},
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    saveItem(item) {},
    removeItem(item, type) {
      this.$confirm("此操作将永久删除该条数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          let obj = type === "game" ? this.form.games : this.form.services;
          // let index = obj.indexOf(item);
          // if (index !== -1) {
          //   obj.splice(index, 1);
          // }
          obj.map((unit, i) => {
            if (unit.id === item.id) {
              obj.splice(i, 1);
            }
          });
        })
        .catch(() => {});
    },
    addItem(type) {
      let games = this.form.games,
        services = this.form.services;
      let newid = type === "game" ? games.length + 1 : services.length + 1;
      let newdata = {
        name: "",
        url: "",
        id: new Date().valueOf()
      };
      if (type === "game") {
        games.push(newdata);
      } else {
        services.push(newdata);
      }
    }
  },
  computed: {}
};
</script>