import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home";
import Club from "./views/Club";
import Group from "./views/ClubViews/Group";
import Setting from "./views/ClubViews/Setting";

import Create from "./views/ClubViews/Create";
import CreateGroup from "./views/ClubViews/Group-Setup";

import Message from "./views/Message";
import MessageWrite from "./views/MessageViews/Write";
import MessageHistory from "./views/MessageViews/History";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/Club",
      name: "Club",
      component: Club,
      redirect: "/Club/Create",
      children: [
        {
          path: "/Club/Create",
          name: "Create",
          component: Create
        },
        {
          path: "/Club/Create/Setup",
          name: "NewSetup",
          component: CreateGroup
        },
        {
          path: "/Club/Group/Setup",
          name: "EditSetup",
          component: CreateGroup
        },
        {
          path: "/Club/Group",
          name: "Group",
          component: Group,
          meta: {
            title: "",
            auth: true
          }
        },
        {
          path: "/Club/Setting",
          name: "Setting",
          component: Setting,
          meta: {
            title: "",
            auth: true
          }
        }
      ]
    },
    {
      path: "/Message",
      name: "Message",
      component: Message,
      redirect: "/Message/Write",
      children: [
        {
          path: "/Message/Write",
          name: "Write",
          component: MessageWrite
        },
        {
          path: "/Message/History",
          name: "History",
          component: MessageHistory
        }
      ]
    }
  ]
});
