import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    pgtit: {
      type: String
    },
    Selected: []
  },
  mutations: {
    setPgtit(state, tit) {
      state.pgtit = tit;
    },
    setSelected(state, arr) {
      state.Selected = arr;
    }
  },
  actions: {
    setPgtitFun(context, tit) {
      context.commit("setPgtit", tit);
    },
    setSelectedFun(context, arr) {
      context.commit("setSelected", arr);
    }
  }
});
