<template>
  <el-container class="create-wrap">
    <el-header class="head">
      <h2 class="pgtit">群聊设置</h2>
    </el-header>
    <el-container>
      <el-main class="main-inner">
        <selectedMain />
      </el-main>
      <el-aside class="aside" width="350px">
        <div class="aside-box aside-set">
          <div class="avatar-lb">修改头像</div>
          <el-upload
            class="avatar-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="form.avatar" :src="form.avatar" class="avatar" />
            <div v-else>
              <i v-if="uploading" class="icon el-icon-loading" />
              <i v-else class="icon el-icon-plus" />
            </div>
          </el-upload>
          <el-form :model="form" label-position="left" label-width="80px">
            <el-form-item label="群聊名称">
              <el-input v-model="form.name" placeholder="请填写群名称" />
            </el-form-item>
            <el-form-item label="群管理员">
              <el-select multiple v-model="form.users" placeholder="请选择管理员">
                <el-option
                  v-for="item in form.admins"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <el-form :model="form" label-position="left" label-width="230px">
            <el-form-item label="群成员是否匿名聊天">
              <el-switch v-model="form.hehe0" :active-value="1" :inactive-value="0" />
            </el-form-item>
            <el-form-item label="是否允许管理员添加好友">
              <el-switch v-model="form.hehe1" :active-value="1" :inactive-value="0" />
            </el-form-item>
            <el-form-item label="是否允许管理员新增/删除成员">
              <el-switch v-model="form.hehe2" :active-value="1" :inactive-value="0" />
            </el-form-item>
          </el-form>
        </div>
      </el-aside>
    </el-container>
    <el-footer class="foot">
      <el-button type="primary">确定</el-button>
    </el-footer>
  </el-container>
</template>

<script>
import selectedMain from "../../components/selected-main.vue";
export default {
  name: "Create",
  data() {
    return {
      uploading: false,
      form: {
        avatar: "",
        name: "",
        admins: [
          {
            value: "1",
            label: "小明"
          },
          {
            value: "2",
            label: "大花"
          },
          {
            value: "3",
            label: "阿诺·施瓦辛格"
          },
          {
            value: "6",
            label: "名字很长很长很长很长"
          },
          {
            value: "7",
            label: "爱新觉罗"
          },
          {
            value: "8",
            label: "欧德修凡克"
          },
          {
            value: "9",
            label: "杨超越"
          },
          {
            value: "10",
            label: "蔡徐坤"
          }
        ],
        hehe0: 1,
        hehe1: 1,
        hehe2: 1,
        hehe3: 0
      }
    };
  },
  computed: {
    users() {
      return this.$store.state.Selected;
    }
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.form.avatar = URL.createObjectURL(file.raw);
      this.uploading = false;
    },
    beforeAvatarUpload(file) {
      this.uploading = true;
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  },
  mounted() {},
  components: {
    selectedMain
  }
};
</script>

