<template>
  <el-container class="create-wrap">
    <el-header class="head">
      <h2 class="pgtit">用户选择</h2>
    </el-header>
    <el-container>
      <el-main class="main-inner">
        <div class="has-aside" v-loading="searchLoading">
          <breadcrumb :data="crumb" />
          <search :data="searchData" />
          <div class="infinite" v-infinite-scroll="load" infinite-scroll-disabled="disabled">
            <component :is="flag" :data="lists" v-on:switchComponent="switchComponent" />
            <p class="infi-footer" v-if="loadingMore">
              <i class="el-icon-loading" /> 加载中...
            </p>
            <p class="infi-footer" v-if="noMore">没有更多了</p>
          </div>
        </div>
      </el-main>
      <el-aside class="aside" width="350px">
        <div class="aside-tit">已选用户列表</div>
        <selectedAside />
      </el-aside>
    </el-container>
    <el-footer class="foot">
      <router-link :to="{name: 'NewSetup'}" class="el-button el-button--primary">下一步</router-link>
    </el-footer>
  </el-container>
</template>

<script>
import breadcrumb from "../../components/breadcrumb.vue";
import search from "../../components/search.vue";
import guild from "../../components/guild.vue";
import selectedAside from "../../components/selected-aside.vue";

const community = () => import("../../components/community.vue");
const people = () => import("../../components/people.vue");

export default {
  name: "Create",
  data() {
    return {
      flag: guild,
      searchData: {
        type: "消息",
        word: ""
      },
      crumb: {
        lv1: null
      },
      searchWord: "",
      lists: 10,
      loadingMore: false,
      searchLoading: false
    };
  },
  components: {
    selectedAside,
    breadcrumb,
    search,
    guild,
    community,
    people
  },
  methods: {
    switchComponent(obj) {
      let that = this;
      that.searchLoading = true;
      let component = obj.component;
      switch (component) {
        case "guild":
          that.getGuild;
          break;
        case "community":
          that.getCommunity(obj.id);
          break;
        case "people":
          that.getPeople(obj.id);
          break;
      }
    },
    onSubmit: function(e) {
      console.log("提交搜索！", this.searchWord);
      this.searchLoading = true;
      return false;
    },
    load() {
      this.loadingMore = true;
      setTimeout(() => {
        this.lists += 5;
        this.loadingMore = false;
      }, 800);
    },
    getGuild() {
      let that = this;
      that.flag = guild;
      that.searchLoading = false;
    },
    getCommunity(gid) {
      //gid为公会ID
      let that = this;
      console.log(gid);
      that.flag = community;
      that.crumb = {
        lv1: "所属公会名称",
        lv1id: 5 //所属公会ID（上级ID）
      };
      that.searchLoading = false;
    },
    getPeople(cid) {
      //cid为社区ID
      let that = this;
      console.log(cid);
      that.flag = people;
      that.crumb = {
        lv1: "所属公会名称",
        lv1id: 5,
        lv2: "所属社区名称",
        lv2id: 8
      };
      that.searchLoading = false;
    }
  },
  mounted() {},
  computed: {
    noMore() {
      return this.lists >= 37;
    },
    disabled() {
      return this.loadingMore || this.noMore;
    }
  }
};
</script>