<template>
  <ul>
    <li v-for="(list, i) in data" :key="i">
      <a @click="toCommunity(i)">
        公会名称{{i}}
        <i class="el-icon-arrow-right" />
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  name: "GuildList",
  data() {
    return {};
  },
  props: {
    data: null
  },
  mounted() {},
  methods: {
    toCommunity(id) {
      //执行父组件的switchComponents方法，并告诉父组件切换到哪个组件，公会的ID是什么
      this.$emit("switchComponent", {
        component: "community",
        id: id
      });
    }
  }
};
</script>