<template>
  <div>
    <div class="selected-main" id="selectedMainList">
      <ul>
        <li v-for="(user, i) in users" :key="i">
          <div class="avatar">
            <img :src="user.avatar" />
            <el-button size="mini" @click="delUser(user.uid)" icon="el-icon-close" />
          </div>
          <span>{{user.username}}</span>
        </li>
        <li class="add-user-box">
          <el-button class="add-user-btn" @click="openDialog = true" icon="el-icon-plus" />
        </li>
      </ul>
    </div>
    <el-dialog title="添加用户" :visible.sync="openDialog" width="730px" class="dialog-eduser">
      <div>
        <EditDialog />
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="openDialog = false">取 消</el-button>
        <el-button type="primary" @click="addUser('群id 888')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import EditDialog from "../views/ClubViews/Edit-Dialog.vue";
export default {
  name: "selected",
  data() {
    return {
      openDialog: false
    };
  },
  components: {
    EditDialog
  },
  computed: {
    users() {
      this.scrollToLast();
      return this.$store.state.Selected;
    }
  },
  created() {},
  mounted() {
    let users = this.$store.state.Selected;
    if (users.length < 1) {
      for (let i = 0; i < 50; i++) {
        let temp = {
          username: "用户" + (1000 + i),
          avatar: require("../assets/img/avatar.jpg"),
          uid: 1000 + i
        };
        users.splice(i, 1, temp);
      }
    }
    this.scrollToLast();
  },
  methods: {
    delUser(uid) {
      let users = this.$store.state.Selected;
      users.map((item, i) => {
        if (item.uid === uid) {
          users.splice(i, 1);
        }
      });
    },
    scrollToLast() {
      this.$nextTick(() => {
        let elm = document.getElementById("selectedMainList");
        elm.scrollTop = elm.scrollHeight;
      });
    },
    addUser(gid) {
      console.log(gid);
      this.openDialog = false;
    }
  }
};
</script>