<template>
  <div>
    <div class="selected-aside" id="selectedAsideList">
      <ul>
        <li v-for="(user, i) in users" :key="i">
          <div class="avatar">
            <img :src="user.avatar" />
            <el-button size="mini" @click="delUser(user.uid)" icon="el-icon-close" />
          </div>
          <span>{{user.username}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "selectedAside",
  computed: {
    users() {
      this.scrollToLast();
      return this.$store.state.Selected;
    }
  },
  mounted() {
    let users = this.$store.state.Selected;
    if (users.length < 1) {
      for (let i = 0; i < 50; i++) {
        let temp = {
          username: "用户" + (1000 + i),
          avatar: require("../assets/img/avatar.jpg"),
          uid: 1000 + i
        };
        users.splice(i, 1, temp);
      }
    }
    this.scrollToLast();
  },
  methods: {
    delUser(uid) {
      let users = this.$store.state.Selected;
      users.map((item, i) => {
        if (item.uid === uid) {
          users.splice(i, 1);
        }
      });
    },
    scrollToLast() {
      this.$nextTick(() => {
        let elm = document.getElementById("selectedAsideList");
        elm.scrollTop = elm.scrollHeight;
      });
    }
  }
};
</script>